main {
    margin: 3% 2% 0 2%;
}

.back {
    display: flex;
    align-items: center;
    justify-content: space-around;
    border-radius: 5px;
    width: 7%;
    height: 4vh;
    margin: 0 2%;
    background-color: white;
    box-shadow: 0 0 10px gray;
    text-decoration: none;
    color: black;
}
.back-dark {
    display: flex;
    align-items: center;
    justify-content: space-around;
    border-radius: 5px;
    width: 7%;
    height: 4vh;
    margin: 0 2%;
    background-color: #2b3743;
    box-shadow: 0 0 10px black;
    text-decoration: none;
    color: white;
}

.card-country {
    display: flex;
    margin: 4% 3% 0 2%;
    justify-content: space-between;   
}

.card-img-country {
    width: 45%;
    border: 1px solid gray; 
}

.card-body-country {
    width: 45%;
}

.card-part {
    display: flex;
    justify-content: space-between;
}

.card-bottom-country {
    display: flex;
    align-items: baseline;
    margin-top: 4%;
}

.card-bottom-list {
    list-style-type: none;
    display: flex;
    width: 70%;
    flex-wrap: wrap;
}

.card-bottom-list li {
    border: 1px solid gray;
    border-radius: 5px;
    box-shadow: 0 0 10px gray;
    margin: 0 0 2% 2%;
    padding: 1.5%;
}

.bold {
    font-weight: bold
}

@media (max-width: 780px){
    .card-country {
        flex-direction: column;
    }
    .card-img-country {
        width: 100%;
    }
    .card-body-country {
        width: 100%;
    }
    .back {
        width: 12%;
    }
    .back-dark {
        width: 12%;
    }
    .card-bottom-country {
        flex-direction: column;
    }
}

@media (max-width: 540px){
    .back {
        width: 25%;
    }
    .back-dark {
        width: 25%;
    }
}